.App {
  text-align: center;
  width: 100%;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
}
.App__header{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 10%;
  position: absolute;
    background: -webkit-linear-gradient( rgba(0,0,0,.3),rgba(248, 248, 248, 0.466));
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}



.App__header_link {
 text-decoration: none;
 color: red;
   width: 50px;
  height: 25px;
margin-left: 50px;
  border: 1px solid rgb(0, 255, 13);
  -webkit-animation: header_button 2s infinite linear alternate;
          animation: header_button 2s infinite linear alternate;

}
.App__header_link::after{
  content:'';
  display: block;
  width: 0%;
  height: 3px;
  border-radius: 50px
}

.App__header_link:hover::after {
  content:'';
  -webkit-animation: App__header_link 0.5s forwards;
          animation: App__header_link 0.5s forwards;  
  background:  rgb(48, 34, 178);
}
.App__1st-slide{
  height: 100%;
  width: 100%;
  background: -webkit-linear-gradient( rgba(0,0,0,.3),rgba(248, 248, 248, 0.466));
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.App__1st-slide_photo{
  width: 40%;
  height: 50%;
  border-radius: 15px;
   -webkit-box-shadow: 0 0 20px rgba(0,0,0,0.5);
           box-shadow: 0 0 20px rgba(0,0,0,0.5);
  border: 1px solid red;
  margin-right: -50px;
  background-image: url(./img/web-trifecta_html5_css3_js-strict.png);
      background-size: contain;
      background-repeat: no-repeat;
}
.App__1st-slide_About{
    width: 40%;
  height: 50%;
    border: 1px solid red;

}

@-webkit-keyframes App__header_link{
  0% {  width: 0%;
    background: rgb(178, 164, 34);
  border-radius: 50px;}
  20%{
       background: rgb(178, 34, 34);
  }
    50%{
       background: rgb(34, 178, 41);}
  100% {width: 100%;
    background: rgb(48, 34, 178);
    border-radius: 50px;}
  }

@keyframes App__header_link{
  0% {  width: 0%;
    background: rgb(178, 164, 34);
  border-radius: 50px;}
  20%{
       background: rgb(178, 34, 34);
  }
    50%{
       background: rgb(34, 178, 41);}
  100% {width: 100%;
    background: rgb(48, 34, 178);
    border-radius: 50px;}
  }



@-webkit-keyframes header_button{
  from {color:blue;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)}
  to {color: red;
   -webkit-transform: rotate(15deg);
           transform: rotate(15deg)}
}



@keyframes header_button{
  from {color:blue;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)}
  to {color: red;
   -webkit-transform: rotate(15deg);
           transform: rotate(15deg)}
}


